window.submitted = false; // Declare globally to track form submission

// Validate Name
function validateName() {
    var name = document.getElementById('name').value;
    if (name.trim().length === 0) {
        alert('Name can\'t be blank');
        return false;
    }
    if (!name.match(/^[a-zA-Z]{3,}(?: [a-zA-Z]+){0,2}$/)) {
        alert('Please enter your correct name');
        return false;
    }
    return true;
}

// Validate Phone
function validatePhone() {
    var phone = document.getElementById('phone').value;
    if (phone.length === 0) return true; // Phone is optional
    if (!phone.match(/^[0]?[789]\d{9}$/)) {
        alert('Please enter a correct phone number');
        return false;
    }
    return true;
}

// Validate Email
function validateEmail() {
    var email = document.getElementById('email').value;
    if (email.length === 0) {
        alert('Email can\'t be blank');
        return false;
    }
    if (!email.match(/^[A-Za-z0-9._%+-]+@[A-Za-z0-9.-]+\.[A-Za-z]{2,4}$/)) {
        alert('Please enter a correct email address');
        return false;
    }
    return true;
}

// Validate Full Form
window.validateForm = function () {
    if (!validateName() || !validatePhone() || !validateEmail()) {
        alert('Form not submitted');
        return false;
    } else {
        window.submitted = true;
        return true;
    }
};

// Handle Popup Logic
document.addEventListener("DOMContentLoaded", function () {
    var popupContainer = document.getElementById('popup-container');
    var closeBtn = document.getElementById('close-btn');
    var contactUsLinks = document.querySelectorAll('#contact-us-link-desktop, #contact-us-link-mobile');

    // Show popup after 5 seconds if not already shown
    if (!sessionStorage.getItem('popupShown')) {
        setTimeout(function () {
            popupContainer.style.display = 'block';
            popupContainer.classList.add('show');
        }, 5000);
    } else {
        popupContainer.style.display = 'none';
    }

    // Close popup event
    closeBtn.addEventListener('click', function () {
        popupContainer.classList.add('hide');
        setTimeout(function () {
            popupContainer.style.display = 'none';
            popupContainer.classList.remove('hide', 'show');
        }, 500);
        sessionStorage.setItem('popupShown', true);
    });

    // Show popup when clicking "Contact Us"
    contactUsLinks.forEach(link => {
        link.addEventListener('click', function (event) {
            event.preventDefault();
            popupContainer.style.display = 'block';
            popupContainer.classList.add('show');
            sessionStorage.setItem('popupShown', true);
        });
    });
});
